import React, { useState } from 'react';
import { formatCardNumber, extractBIN } from '@/lib/binGenerator';

interface DownloadButtonProps {
  bins: string[];
}

const DownloadButton: React.FC<DownloadButtonProps> = ({ bins }) => {
  const [isDownloading, setIsDownloading] = useState(false);

  const handleDownload = async () => {
    if (!bins || bins.length === 0) return;
    
    setIsDownloading(true);
    
    try {
      // Create formatted content for the text file
      const timestamp = new Date().toISOString().replace(/[:.]/g, '-');
      const header = `BIN Generator - Educational Tool
Generated: ${new Date().toLocaleString()}
Total Records: ${bins.length}
Purpose: Educational/Demonstration Only

=== GENERATED BINS ===

`;

      const binData = bins.map((bin, index) => {
        const binNumber = extractBIN(bin);
        const formattedNumber = formatCardNumber(bin);
        return `${index + 1}. BIN: ${binNumber} | Full: ${bin} | Formatted: ${formattedNumber}`;
      }).join('\n');

      const footer = `

=== DISCLAIMER ===
These numbers are generated for educational purposes only.
They demonstrate the Luhn algorithm validation process.
DO NOT use for actual transactions or illegal activities.
Generated by BIN Educational Tool - ${timestamp}`;

      const textData = header + binData + footer;
      
      // Create and download the file
      const blob = new Blob([textData], { type: "text/plain;charset=utf-8" });
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = `bins_educational_${timestamp}.txt`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      window.URL.revokeObjectURL(url);
      
      // Simulate download delay for better UX
      await new Promise(resolve => setTimeout(resolve, 1000));
    } catch (error) {
      console.error('Download failed:', error);
    } finally {
      setIsDownloading(false);
    }
  };

  const isDisabled = !bins || bins.length === 0 || isDownloading;

  return (
    <div className="space-y-4">
      {/* Download Button */}
      <button
        onClick={handleDownload}
        disabled={isDisabled}
        className={`w-full p-4 font-bold text-lg rounded-lg transition-all duration-300 transform
                   ${isDisabled
                     ? 'bg-gray-700 text-gray-400 cursor-not-allowed border-2 border-gray-600'
                     : 'bg-gradient-to-r from-purple-600 to-pink-600 text-white hover:from-purple-500 hover:to-pink-500 border-2 border-purple-400 hover:scale-105 shadow-lg shadow-purple-500/30 hover:shadow-purple-400/50'
                   }`}
        style={{ fontFamily: 'Orbitron, monospace' }}
      >
        {isDownloading ? (
          <div className="flex items-center justify-center space-x-2">
            <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
            <span>DOWNLOADING...</span>
          </div>
        ) : (
          <div className="flex items-center justify-center space-x-2">
            <span>📥</span>
            <span>DOWNLOAD AS TXT</span>
          </div>
        )}
      </button>

      {/* Download Info */}
      {bins && bins.length > 0 && (
        <div className="p-4 bg-purple-900/20 border border-purple-500/30 rounded-lg backdrop-blur-sm">
          <div className="text-purple-300 text-sm space-y-2">
            <div className="flex justify-between items-center">
              <span>Ready to download:</span>
              <span className="font-bold text-purple-400">{bins.length} BINs</span>
            </div>
            <div className="text-xs text-purple-400 space-y-1">
              <p>• File includes formatted numbers and BIN extraction</p>
              <p>• Contains educational disclaimer and timestamp</p>
              <p>• Format: Plain text (.txt) for easy viewing</p>
            </div>
          </div>
        </div>
      )}

      {/* No Data Message */}
      {(!bins || bins.length === 0) && (
        <div className="p-4 bg-gray-900/50 border border-gray-600/30 rounded-lg backdrop-blur-sm">
          <div className="text-gray-400 text-sm text-center">
            <p>Generate some BINs first to enable download</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default DownloadButton;
