/**
 * Validates a credit card number using the Luhn algorithm
 * @param cardNumber - The card number to validate
 * @returns boolean - True if valid, false otherwise
 */
export const luhnCheck = (cardNumber: string): boolean => {
  let sum = 0;
  let shouldDouble = false;
  
  // Process digits from right to left
  for (let i = cardNumber.length - 1; i >= 0; i--) {
    let digit = parseInt(cardNumber.charAt(i), 10);
    
    if (shouldDouble) {
      digit *= 2;
      if (digit > 9) {
        digit -= 9;
      }
    }
    
    sum += digit;
    shouldDouble = !shouldDouble;
  }
  
  return sum % 10 === 0;
};

/**
 * Generates a valid credit card number from a BIN prefix
 * @param prefix - The BIN prefix (first 6 digits)
 * @param length - Total length of the card number (default: 16)
 * @returns string - A valid credit card number
 */
export const generateCardNumber = (prefix: string, length: number = 16): string => {
  if (prefix.length >= length) {
    throw new Error("Prefix length cannot be greater than or equal to total length");
  }
  
  const numberLength = length - prefix.length - 1; // -1 for check digit
  let cardNumber = prefix;
  
  // Generate random digits for the middle part
  for (let i = 0; i < numberLength; i++) {
    cardNumber += Math.floor(Math.random() * 10).toString();
  }
  
  // Calculate and append the check digit using Luhn algorithm
  for (let checkDigit = 0; checkDigit <= 9; checkDigit++) {
    const fullNumber = cardNumber + checkDigit.toString();
    if (luhnCheck(fullNumber)) {
      return fullNumber;
    }
  }
  
  throw new Error("Unable to generate valid card number");
};

/**
 * Generates multiple BIN numbers for a given country and VBV status
 * @param country - The country code
 * @param isVBV - Whether to generate VBV or non-VBV BINs
 * @param count - Number of BINs to generate
 * @returns string[] - Array of generated card numbers
 */
export const generateBinNumbers = (country: string, isVBV: boolean, count: number): string[] => {
  const mapping = isVBV ? vbvMap : nonVBVMap;
  const prefixes = mapping[country];
  
  if (!prefixes || prefixes.length === 0) {
    throw new Error(`No BIN prefixes available for ${country}`);
  }
  
  const bins: string[] = [];
  
  for (let i = 0; i < count; i++) {
    try {
      // Select a random prefix from the available ones
      const randomPrefix = prefixes[Math.floor(Math.random() * prefixes.length)];
      const cardNumber = generateCardNumber(randomPrefix);
      
      // Verify the generated number is valid
      if (luhnCheck(cardNumber)) {
        bins.push(cardNumber);
      } else {
        // If somehow invalid, try again
        i--;
      }
    } catch (error) {
      console.error(`Error generating card number: ${error}`);
      // Try again with a different prefix
      i--;
    }
  }
  
  return bins;
};

/**
 * Gets available countries for BIN generation
 * @returns string[] - Array of available country codes
 */
export const getAvailableCountries = (): string[] => {
  return Object.keys(nonVBVMap);
};

/**
 * Formats a card number with spaces for better readability
 * @param cardNumber - The card number to format
 * @returns string - Formatted card number
 */
export const formatCardNumber = (cardNumber: string): string => {
  return cardNumber.replace(/(.{4})/g, '$1 ').trim();
};

/**
 * Extracts BIN (first 6 digits) from a card number
 * @param cardNumber - The full card number
 * @returns string - The BIN portion
 */
export const extractBIN = (cardNumber: string): string => {
  return cardNumber.substring(0, 6);
};
