import React from 'react';
import { getAvailableCountries } from '@/lib/binGenerator';

interface BinFilterProps {
  selectedCountry: string;
  setCountry: (country: string) => void;
  isVBV: boolean;
  setIsVBV: (flag: boolean) => void;
  onGenerate: () => void;
  isGenerating: boolean;
}

const BinFilter: React.FC<BinFilterProps> = ({
  selectedCountry,
  setCountry,
  isVBV,
  setIsVBV,
  onGenerate,
  isGenerating,
}) => {
  const countries = getAvailableCountries();

  return (
    <div className="p-6 border-2 border-green-500/50 rounded-lg bg-gradient-to-br from-gray-900/80 to-black/80 backdrop-blur-sm shadow-2xl">
      <h2 className="text-2xl font-bold mb-6 text-green-400" style={{ fontFamily: 'Orbitron, monospace' }}>
        CONFIGURATION PANEL
      </h2>
      
      {/* Country Selection */}
      <div className="mb-6">
        <label className="block mb-3 text-green-300 font-semibold">
          SELECT TARGET COUNTRY:
        </label>
        <select
          value={selectedCountry}
          onChange={(e) => setCountry(e.target.value)}
          className="w-full p-4 bg-black/80 text-green-400 border-2 border-green-600/50 rounded-lg 
                     focus:border-green-400 focus:outline-none focus:ring-2 focus:ring-green-400/20
                     hover:border-green-500 transition-all duration-300 font-mono text-lg
                     shadow-inner"
          style={{ fontFamily: 'Fira Code, monospace' }}
        >
          <option value="" className="bg-black text-green-400">-- SELECT COUNTRY --</option>
          {countries.map((country) => (
            <option key={country} value={country} className="bg-black text-green-400">
              {country}
            </option>
          ))}
        </select>
      </div>

      {/* VBV Selection */}
      <div className="mb-6">
        <label className="block mb-3 text-green-300 font-semibold">
          VERIFICATION MODE:
        </label>
        <div className="flex space-x-6">
          <label className="flex items-center cursor-pointer group">
            <input
              type="radio"
              checked={!isVBV}
              onChange={() => setIsVBV(false)}
              className="sr-only"
            />
            <div className={`w-6 h-6 rounded-full border-2 mr-3 flex items-center justify-center transition-all duration-300 ${
              !isVBV 
                ? 'border-green-400 bg-green-400 shadow-lg shadow-green-400/50' 
                : 'border-green-600 hover:border-green-500'
            }`}>
              {!isVBV && <div className="w-2 h-2 rounded-full bg-black"></div>}
            </div>
            <span className={`font-mono text-lg transition-colors duration-300 ${
              !isVBV ? 'text-green-400' : 'text-green-500 group-hover:text-green-400'
            }`}>
              NON-VBV
            </span>
          </label>
          
          <label className="flex items-center cursor-pointer group">
            <input
              type="radio"
              checked={isVBV}
              onChange={() => setIsVBV(true)}
              className="sr-only"
            />
            <div className={`w-6 h-6 rounded-full border-2 mr-3 flex items-center justify-center transition-all duration-300 ${
              isVBV 
                ? 'border-cyan-400 bg-cyan-400 shadow-lg shadow-cyan-400/50' 
                : 'border-green-600 hover:border-green-500'
            }`}>
              {isVBV && <div className="w-2 h-2 rounded-full bg-black"></div>}
            </div>
            <span className={`font-mono text-lg transition-colors duration-300 ${
              isVBV ? 'text-cyan-400' : 'text-green-500 group-hover:text-green-400'
            }`}>
              VBV
            </span>
          </label>
        </div>
      </div>

      {/* Generate Button */}
      <button
        onClick={onGenerate}
        disabled={isGenerating || !selectedCountry}
        className={`w-full p-4 font-bold text-lg rounded-lg transition-all duration-300 transform
                   ${isGenerating || !selectedCountry
                     ? 'bg-gray-700 text-gray-400 cursor-not-allowed border-2 border-gray-600'
                     : 'bg-gradient-to-r from-green-600 to-green-500 text-black hover:from-green-500 hover:to-green-400 border-2 border-green-400 hover:scale-105 shadow-lg shadow-green-500/30 hover:shadow-green-400/50'
                   }`}
        style={{ fontFamily: 'Orbitron, monospace' }}
      >
        {isGenerating ? (
          <div className="flex items-center justify-center space-x-2">
            <div className="w-5 h-5 border-2 border-gray-400 border-t-transparent rounded-full animate-spin"></div>
            <span>GENERATING...</span>
          </div>
        ) : (
          'GENERATE BINS'
        )}
      </button>

      {/* Info Panel */}
      <div className="mt-6 p-4 bg-black/60 border border-green-600/30 rounded-lg">
        <div className="text-green-300 text-sm space-y-1">
          <p><span className="text-green-400">•</span> Each generation creates 20 valid BIN numbers</p>
          <p><span className="text-green-400">•</span> All numbers pass Luhn algorithm validation</p>
          <p><span className="text-green-400">•</span> BIN = First 6 digits (Bank Identification Number)</p>
        </div>
      </div>
    </div>
  );
};

export default BinFilter;
