import React from 'react';
import { formatCardNumber, extractBIN } from '@/lib/binGenerator';

interface BinDisplayProps {
  bins: string[];
  isGenerating: boolean;
}

const BinDisplay: React.FC<BinDisplayProps> = ({ bins, isGenerating }) => {
  if (isGenerating) {
    return (
      <div className="p-6 border-2 border-cyan-500/50 rounded-lg bg-gradient-to-br from-gray-900/80 to-black/80 backdrop-blur-sm shadow-2xl">
        <h2 className="text-2xl font-bold mb-6 text-cyan-400" style={{ fontFamily: 'Orbitron, monospace' }}>
          GENERATED BINS
        </h2>
        <div className="flex items-center justify-center h-64">
          <div className="text-center">
            <div className="w-16 h-16 border-4 border-cyan-400 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
            <p className="text-cyan-300 text-lg font-mono">Generating BINs...</p>
            <div className="mt-4 flex justify-center space-x-1">
              <div className="w-2 h-2 bg-cyan-400 rounded-full animate-pulse"></div>
              <div className="w-2 h-2 bg-cyan-400 rounded-full animate-pulse" style={{ animationDelay: '0.2s' }}></div>
              <div className="w-2 h-2 bg-cyan-400 rounded-full animate-pulse" style={{ animationDelay: '0.4s' }}></div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (!bins || bins.length === 0) {
    return (
      <div className="p-6 border-2 border-gray-600/50 rounded-lg bg-gradient-to-br from-gray-900/80 to-black/80 backdrop-blur-sm shadow-2xl">
        <h2 className="text-2xl font-bold mb-6 text-gray-400" style={{ fontFamily: 'Orbitron, monospace' }}>
          GENERATED BINS
        </h2>
        <div className="flex items-center justify-center h-64">
          <div className="text-center">
            <div className="text-6xl mb-4 opacity-30">💳</div>
            <p className="text-gray-500 text-lg font-mono">No BINs generated yet</p>
            <p className="text-gray-600 text-sm mt-2">Configure settings and click "GENERATE BINS"</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6 border-2 border-cyan-500/50 rounded-lg bg-gradient-to-br from-gray-900/80 to-black/80 backdrop-blur-sm shadow-2xl">
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold text-cyan-400" style={{ fontFamily: 'Orbitron, monospace' }}>
          GENERATED BINS
        </h2>
        <div className="text-cyan-300 text-sm font-mono bg-cyan-900/30 px-3 py-1 rounded-full border border-cyan-500/30">
          {bins.length} RECORDS
        </div>
      </div>

      <div className="bg-black/60 rounded-lg border border-cyan-600/30 overflow-hidden">
        {/* Header */}
        <div className="bg-gradient-to-r from-cyan-900/50 to-cyan-800/50 p-4 border-b border-cyan-600/30">
          <div className="grid grid-cols-3 gap-4 text-cyan-300 font-semibold text-sm">
            <div>BIN (First 6)</div>
            <div>FULL NUMBER</div>
            <div>STATUS</div>
          </div>
        </div>

        {/* Content */}
        <div className="max-h-96 overflow-y-auto custom-scrollbar">
          {bins.map((bin, index) => {
            const binNumber = extractBIN(bin);
            const formattedNumber = formatCardNumber(bin);
            
            return (
              <div 
                key={index} 
                className="p-4 border-b border-cyan-600/20 hover:bg-cyan-900/20 transition-colors duration-200 group"
              >
                <div className="grid grid-cols-3 gap-4 items-center">
                  {/* BIN */}
                  <div className="font-mono text-cyan-400 font-bold text-lg">
                    {binNumber}
                  </div>
                  
                  {/* Full Number */}
                  <div className="font-mono text-green-300 text-sm">
                    <span className="text-cyan-400 font-bold">{binNumber}</span>
                    <span className="text-green-300">{bin.slice(6)}</span>
                  </div>
                  
                  {/* Status */}
                  <div className="flex items-center space-x-2">
                    <div className="w-2 h-2 bg-green-400 rounded-full animate-pulse"></div>
                    <span className="text-green-400 text-xs font-mono">VALID</span>
                  </div>
                </div>
                
                {/* Formatted display on hover */}
                <div className="mt-2 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
                  <div className="text-xs text-gray-400 font-mono">
                    Formatted: {formattedNumber}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* Footer Info */}
      <div className="mt-4 p-3 bg-black/40 border border-cyan-600/20 rounded-lg">
        <div className="text-cyan-300 text-xs space-y-1">
          <p><span className="text-cyan-400">•</span> All numbers are Luhn algorithm validated</p>
          <p><span className="text-cyan-400">•</span> Hover over entries to see formatted display</p>
          <p><span className="text-cyan-400">•</span> Use download button to export as TXT file</p>
        </div>
      </div>
    </div>
  );
};

export default BinDisplay;
